@import "../_mixins/mixins_list"
/* header */
.header
  position: fixed
  top: 0
  left: 0
  width: 100%
  z-index: 10
  padding: 20px 15px
  transition: all .3s
  @media (min-width: 1200px)
    padding: 20px 30px
  &.scroll
    padding: 10px 15px
    background-color: $txt-color
    @media (min-width: 1200px)
      padding: 10px 30px
  &__content
    display: flex
    justify-content: space-between
    .logo-link
      color: $accent-color
      display: inline-block
      font-family: Montserrat, sans-serif
      font-weight: 600
      @include adaptive-value('font-size', 24, 16, 1)
      line-height: 24px
      margin: 0 auto 0 0
      order: -2
      position: relative
      z-index: 10
      span
        color: #fff
    /* hamburger */
    @import "../_plugins/hamburger"
    /* account */
    .account
      display: flex
      color: $accent-color
      margin: 0 0 0 20px
      position: relative
      z-index: 10
      .icon
        font-size: 25px
        line-height: 25px
      .cart-link
        position: relative
        display: inline-block
        margin-right: 20px
        padding-right: 10px
        .cart-count
          position: absolute
          top: -2px
          right: -12px
          font-size: 12px
          line-height: 12px
          font-weight: 700
          min-width: 20px
          text-align: left
      .account-menu
        position: relative
        .account-link
          cursor: pointer
        &__list
          position: absolute
          top: calc(100% + 12px)
          right: 0
          background-color: rgba(26,26,26,.97)
          padding: 10px
          border-radius: 5px
        &__item
          margin-bottom: 5px
          &:last-child
            margin-bottom: 0
        &__link
          font-size: 14px
          line-height: 20px
          color: #999
          transition: color .3s
          &:hover
            color: #fff

    .phone-link
      color: #fff
      font-weight: 600
      font-size: 22px
      line-height: 24px
      margin-right: 20px
      letter-spacing: .5px
      position: relative
      &:after
        content: ''
        display: block
        width: 0
        height: 1px
        background-color: transparent
        position: absolute
        bottom: -2px
        left: 0
        transition: background-color .3s, width .3s
      &:hover
        &:after
          width: 100%
          background-color: $accent-color
      @media (min-width: 768px)
        display: inline-block !important
        order: -1
        margin-right: 10px
        white-space: nowrap
      @media (min-width: 1200px)
        order: 0
        margin-left: 40px
    .main-menu
      position: absolute
      top: 0
      left: 0
      width: 100%
      background-color: rgba($txt-color, .97)
      padding-top: 65px
      min-height: 0
      overflow: hidden
      transition: min-height .3s
      &.active
        min-height: 100vh
      @media (min-width: 1200px)
        display: block !important
        position: relative
        padding-top: 0
        min-height: auto
        flex: 1 1 auto
        background-color: transparent
      &__list
        display: flex
        flex-direction: column
        color: #fff
        padding: 15px 0
        text-transform: uppercase
        font-size: 18px
        line-height: 20px
        text-align: center

        @media (min-width: 1200px)
          flex-direction: row
          flex-wrap: wrap
          justify-content: flex-end
          padding: 0
      &__item
        display: block
        border-bottom: 1px solid #222
        padding: 10px 15px
        @media (min-width: 1200px)
          border-bottom: none
          padding: 0
          display: inline-block
          margin-left: 20px
          &:nth-of-type(1)
            margin-left: 0
        &:last-child
          border-bottom: none
        &.phone
          .phone-link
            margin: 0 auto
          @media (min-width: 768px)
            display: none !important
      &__link
        display: inline-block
        transition: color .3s
        @media (min-width: 1200px)
          color: #999
          font-weight: 600
          font-size: 14px
          line-height: 25px
          letter-spacing: .5px
        &:hover
          color: $accent-color
          @media (min-width: 1200px)
            color: #fff
  & + main, & + section
    padding-top: 80px
  & + .full-page, & + .single-page
    padding-top: 110px