@import "settings"                                                                                                      // settings
@import "_mixins/mixins_list"                                                                                           // mixins
@import "_fonts/fonts_list"                                                                                             // fonts
@import "_fonts/fontello"                                                                                            // icons font
@import "_main/reset"                                                                                                  // reset styles
@import "_main/header"                                                                                                 // header

//* pages *//
/* main page */
.main-block
  background-repeat: no-repeat
  background-position: center
  background-size: cover
  background-color: #4e4846
  position: relative
  min-height: 100vh
  &:before
    content: ''
    display: block
    width: 100%
    height: 100%
    background-color: rgba(0,0,0,.3)
    position: absolute
    top: 0
    left: 0
  &__content
    display: flex
    flex-direction: column
    align-items: flex-start
    justify-content: flex-end
    flex-wrap: nowrap
    color: #fff
    position: relative
    z-index: 1
    min-height: calc(100vh - 80px)
    @include adaptive-value('padding-bottom', 60, 35, 1)
  &__subtitle
    font-family: Montserrat, sans-serif
    font-weight: 400
    @include adaptive-value('font-size', 24, 16, 1)
    @include adaptive-value('line-height', 24, 20, 1)
    display: inline-block
    margin-bottom: 10px
  &__title
    font-family: Montserrat, sans-serif
    font-weight: 600
    @include adaptive-value('font-size', 45, 18, 1)
    @include adaptive-value('line-height', 45, 24, 1)
    display: inline-block
    margin-bottom: 10px
    @media (min-width: 1024px)
      letter-spacing: 1px
  &__descr
    @include adaptive-value('font-size', 19, 14, 1)
    margin-bottom: 30px
    @media (min-width: 415px)
      margin-top: 15px
    @media (min-width: 1200px)
      margin-bottom: 50px
    p
      margin-bottom: 10px
  .enterprises-link
    display: block
    width: 100%
    border: 1px solid #aaa
    padding: 15px 0
    font-weight: 600
    letter-spacing: .5px
    text-align: center
    color: #fff
    position: relative
    overflow: hidden
    transition: border-color .3s
    text-transform: uppercase
    background-color: rgba(0,0,0,.4)
    @media (min-width: 768px)
      max-width: 300px
      margin: 0 auto
    span
      position: relative
      z-index: 2
    &:before
      content: ''
      display: block
      width: 100%
      height: 100%
      position: absolute
      background-color: $accent-color
      top: 0
      left: 0
      transform: translateY(100%)
      transition: transform .3s
    &:hover
      border-color: $accent-color
      &:before
        transform: translateY(0)


/* contacts page */
.contacts
  background-repeat: no-repeat
  background-position: center
  background-size: cover
  background-color: #4e4846
  position: relative
  &:before
    content: ''
    display: block
    width: 100%
    height: 100%
    background-color: rgba(0,0,0,.3)
    position: absolute
    top: 0
    left: 0
  &__content
    position: relative
    display: flex
    flex: 1 1 100%
    flex-direction: column
    align-items: flex-start
    justify-content: center
    flex-wrap: nowrap
    z-index: 1
    color: #fff
    @include adaptive-value('padding-bottom', 60, 35, 1)
  .contact-title
    text-transform: uppercase
    font-family: Montserrat, sans-serif
    font-weight: 600
    @include adaptive-value('font-size', 45, 25, 1)
    @include adaptive-value('line-height', 55, 25, 1)
  &__links
    margin: 30px 0
    font-size: 18px
    line-height: 20px
    @media (min-width: 1200px)
      font-size: 20px
      line-height: 24px
    p
      margin-bottom: 10px
      &:last-child
        margin-bottom: 0
      a
        display: inline-block
        margin-left: 10px
        transition: color .3s
        &:hover
          color: $accent-color
  &__form
    width: 100%
    margin-top: 35px
    form
      margin-top: 20px
      @media (min-width: 576px)
        max-width: 420px
      @media (min-width: 768px)
        max-width: 100%
        label
          font-size: 18px
      .input-row
        flex-direction: column
        @media (min-width: 768px)
          flex-direction: row
          .input-group
            flex: 1 1 auto
            margin: 20px 10px
            @media (min-width: 1200px)
              margin: 30px 20px
      .input-group
        @media (min-width: 768px)
          margin: 20px 10px
        @media (min-width: 1200px)
          margin: 30px 20px
      .input-group.checkgroup
        @media (min-width: 768px)
          margin: 40px 10px
        @media (min-width: 1200px)
          margin: 40px 20px
      .input-group.btn-group
        margin: 0
        button
          margin: 0 auto
          @media (min-width: 768px)
            max-width: 320px


/* stocks page */
.stocks-header
  background-repeat: no-repeat
  background-position: center
  background-size: cover
  background-color: #4e4846
  position: relative
  &:before
    content: ''
    display: block
    width: 100%
    height: 100%
    background-color: rgba(0,0,0,.3)
    position: absolute
    top: 0
    left: 0
  &__content
    position: relative
    display: flex
    flex: 1 1 100%
    flex-direction: column
    align-items: flex-start
    justify-content: flex-end
    flex-wrap: nowrap
    z-index: 1
    color: #fff
    @include adaptive-value('padding-bottom', 60, 35, 1)
  &__title
    text-transform: uppercase
    font-family: Montserrat, sans-serif
    font-weight: 600
    @include adaptive-value('font-size', 45, 22, 1)
    @include adaptive-value('line-height', 45, 24, 1)
    display: inline-block
    @include adaptive-value('margin-bottom', 30, 15, 1)
  &__descr
    width: 100%
    max-width: 650px
    @include adaptive-value('font-size', 19, 15, 1)
    @include adaptive-value('line-height', 28, 22, 1)
    p
      margin-bottom: 15px
      &:last-child
        margin-bottom: 0


/* catalog page */
.catalog
  &__title
    @media (min-width: 1024px)
      flex: 1 1 100%
  &__content
    margin-bottom: 15px
    @media (min-width: 1024px)
      display: flex
      flex-wrap: wrap
      align-items: stretch
    &__left-side
      margin: 15px 0
      border-bottom: 1px solid #ccc
      @media (min-width: 1024px)
        width: 100%
        max-width: 230px
        flex: 0 0 230px
        border: none
        border-right: 1px solid #eee
        margin: 0 20px 0 0
      .filter-link
        display: flex
        flex: 1 1 100%
        padding: 10px 0
        font-size: 18px
        line-height: 20px
        font-weight: 600
        @media (min-width: 1024px)
          display: none
        .icon
          margin-right: 10px
          font-size: 24px
          line-height: 20px
          &.icon-filter
            color: rgba($txt-color,.3)
          &.icon-down-open-mini
            margin: 0 0 0 auto
            font-size: 24px
            line-height: 20px
            transform: scale(1,1)
            transition: transform .3s

        &.open
          .icon-down-open-mini
            transform: scale(1,-1)
      .product-filters-block
        border-top: 1px solid #ccc
        @media (min-width: 1024px)
          display: block !important
          border-top: none
          padding: 0 20px 0 0
    &__right-side
      @media (min-width: 1024px)
        flex: 1 1 calc(100% - 250px)
        max-width: calc(100% - 250px)
        .catalog__title
          margin-bottom: 10px
      .category-menu
        margin: 15px 0
        &__list
          position: relative
          padding: 0 20px
          .slide-item
            text-align: center
            .item-icon
              display: flex
              width: 70px
              height: 70px
              align-items: center
              justify-content: center
              margin: 0 auto
            .item-title
              font-size: 14px
              line-height: 14px
              text-align: center
          .owl-nav
            display: flex
            width: 100%
            position: absolute
            top: calc(50% - 12.5px)
            left: 0
            justify-content: space-between
            font-size: 25px
            line-height: 25px
            height: 0px
        &__link
          display: flex
          flex-wrap: wrap
          align-items: center
          justify-content: center
          flex-direction: column
          .item-title
            color: $txt-color
            transition: color .3s
          &:hover
            .item-title
              color: $accent-color
      .catalog__filter
        margin: 30px 0 0
        display: flex
        align-items: center
        justify-content: flex-start
        flex-wrap: wrap
        border-bottom: 1px solid #eee
        padding-bottom: 10px
        @media (min-width: 640px)
          justify-content: space-between
          border-bottom: 1px solid #eee
          padding-bottom: 15px
          margin-top: 20px
        &__sort
          display: flex
          flex: 1 1 auto
          align-items: center
          .icon-signal
            color: rgba($txt-color, .2)
            display: inline-block
            margin-right: 10px
            position: relative
            &:before
              transform: rotate(90deg) scale(-1,1)
          .filter-sort
            margin-right: 40px
            position: relative
            &__link
              color: $accent-color
              display: inline-block
              position: relative
              border-bottom: 1px dotted $accent-color
              font-size: 14px
              line-height: 20px
              @media (min-width: 576px)
                line-height: 14px
              &:after
                position: absolute
                top: 6px
                right: -10px
                content: "\e80a"
                font-family: Fontello
                font-style: normal
                font-weight: 400
                speak: never
                display: inline-block
                margin: 0 auto
                text-decoration: inherit
                text-align: center
                font-variant: normal
                text-transform: none
                display: block
                width: 10px
                height: 10px
            &__list
              position: absolute
              top: calc(100% + 5px)
              left: -10px
              background-color: #fff
              font-size: 14px
              line-height: 20px
              border: 1px solid #ccc
              z-index: 2
              &__item
                border-bottom: 1px solid #ccc
                &:last-child
                  border-bottom: none
              &__type
                display: block
                white-space: nowrap
                padding: 5px 10px
                &:hover
                  color: $accent-color

          .filter-quantity
            position: relative
            @media (min-width: 768px)
              display: block !important
            &__link
              color: $accent-color
              display: inline-block
              position: relative
              border-bottom: 1px dotted $accent-color
              font-size: 14px
              line-height: 20px
              @media (min-width: 576px)
                line-height: 14px
              &:after
                position: absolute
                top: 1px
                right: -15px
                content: "\e80a"
                font-family: Fontello
                font-style: normal
                font-weight: 400
                speak: never
                display: inline-block
                margin: 0 auto
                text-decoration: inherit
                text-align: center
                font-variant: normal
                text-transform: none
                display: block
                width: 10px
                height: 10px
            &__list
              position: absolute
              top: calc(100% + 5px)
              left: -10px
              background-color: #fff
              font-size: 14px
              line-height: 20px
              border: 1px solid #ccc
              z-index: 2
              &__item
                border-bottom: 1px solid #ccc
                &:last-child
                  border-bottom: none
              &__type
                display: block
                white-space: nowrap
                padding: 5px 10px
                &:hover
                  color: $accent-color

          @media (min-width: 576px)
            flex: 1 1 auto
            margin-bottom: 0
        &__view
          display: flex
          align-items: center
          justify-content: flex-end
          flex: 1 1 auto
          .icon
            color: rgba($txt-color, .2)
            transition: color .3s
            font-size: 18px
            line-height: 20px
          .filter-view-type
            display: inline-block
            margin-left: 15px
            &:nth-of-type(1)
              margin-left: 0
            &.active
              .icon
                color: $accent-color
          @media (min-width: 640px)
            flex: 1 1 auto
            justify-content: flex-end
        .filter-name
          display: flex
          align-items: center
          font-size: 14px
          line-height: 14px
          @media (min-width: 576px)
            margin-right: 15px
            span
              display: inline-block !important

      .catalog-list
        margin-top: 15px
        display: flex
        align-items: stretch
        flex-wrap: wrap
        flex: 1 1 100%


/* product page */
.product
  &__title
    @media (min-width: 576px)
      flex: 1 1 100%
  &__content
    display: flex
    flex-wrap: wrap
    margin-bottom: 20px
    .product-comments
      display: flex
      flex-wrap: wrap
      margin: 10px 0
      @media (min-width: 576px)
        flex: 0 0 100%
    .product-rating
      display: flex
      color: $accent-color
      flex: 1 1 100%
      @media (min-width: 576px)
        flex: 0 0 auto
        margin-right: 20px
      .icon
        display: inline-block
        margin-right: 3px
        @media (min-width: 1200px)
          margin-right: 4px
        &:last-child
          margin-right: 0
    .product-answers,.product-questions
      display: inline-block
      color: $txt-color
      transition: color .3s
      margin-top: 10px
      position: relative
      @media (min-width: 576px)
        margin-top: 0
      &:after
        content: ''
        display: block
        width: 0
        height: 1px
        background-color: $accent-color
        transition: width .3s
      &:hover
        &:after
          width: 100%
      span
        color: $accent-color
        display: inline-block
        margin-right: 3px
    .product-questions
      margin-left: 20px
    &__left-side
      flex: 1 1 100%
      width: 100%
      @media (min-width: 768px)
        flex: 0 0 450px
        max-width: 450px
      @media (min-width: 1024px)
        display: flex
        flex: 1 1 auto
        max-width: initial
        align-items: stretch
        justify-content: flex-start
        width: auto
      .slider-block
        display: flex
        flex-direction: column
        flex: 1 1 100%
        @media (min-width: 576px)
          flex-direction: row
          margin: 15px 0
          align-items: stretch
        @media (min-width: 1024px)
          flex: 1 0 450px
          max-width: 450px
        .slick-main
          order: -1
          flex: 1 1 100%
          width: 100%
          @media (min-width: 576px)
            order: 1
            flex: 0 0 calc(100% - 70px)
            max-width: calc(100% - 70px)
            margin-left: 10px
        .slick-thumbs
          flex: 1 1 100%
          width: 100%
          padding: 0 25px
          position: relative
          margin-top: 10px
          @media (min-width: 576px)
            flex: 0 0 60px
            padding: 32px 0
            margin: 0
            max-height: 380px
          .slide-image
            display: block
            width: auto
            max-width: 100%
            margin: 0 auto
            cursor: pointer
          .icon
            position: absolute
            font-size: 25px
            line-height: 30px
            left: 0
            top: calc(50% - 16px)
            cursor: pointer
            @media (min-width: 576px)
              top: -5px
              left: calc(50% - 6px)
              transform: rotate(90deg)
              height: 30px
              width: 12px
            &.next-btn
              left: auto
              right: 0
              @media (min-width: 576px)
                right: auto
                left: calc(50% - 6px)
                top: auto
                bottom: -5px
      .descr-block
        @media (min-width: 1024px)
          display: block !important
          flex: 1 0 auto
          margin: 15px 0 15px 15px
          width: 100%
          max-width: 230px
          .descr-list
            font-size: 14px
            line-height: 20px
            &__item
              display: flex
              flex-wrap: wrap
              justify-content: space-between
              border-bottom: 1px dotted #999
              margin-bottom: 15px
              &:last-child
                margin-bottom: 0
              span
                display: inline-block
                background-color: #fff
                margin-bottom: -7px
                &:nth-of-type(1)
                  padding-right: 5px
                  color: #999
                &:nth-of-type(2)
                  padding-left: 5px
          .more-descr-link
            display: inline-block
            margin-top: 25px
            color: $accent-color
        @media (min-width: 1200px)
          margin: 15px auto
    &__right-side
      flex: 1 1 100%
      width: 100%
      margin-top: 10px
      padding: 10px 0
      border-top: 1px solid #eee
      @media (min-width: 768px)
        flex: 1 1 calc(100% -  450px)
        max-width: calc(100% - 450px)
        margin-top: 15px
        border-top: none
        padding: 0 0 0 15px
      @media (min-width: 960px)
        padding-left: 40px
      @media (min-width: 1024px)
        padding-left: 15px
        flex: 0 1 auto
        width: auto
        max-width: 285px
        position: relative
        &:before
          content: ''
          display: block
          width: 1px
          height: 100%
          position: absolute
          top: 0
          left: 0
          background-color: #ccc
      @media (min-width: 1200px)
        max-width: 400px
      .price-block
        display: flex
        flex-wrap: wrap
        flex: 1 1 100%
        span
          font-size: 24px
          line-height: 24px
          font-weight: 700
          &.price
            color: $txt-color
          &.old-price
            text-decoration: line-through
            color: rgba($txt-color, .5)
            margin-right: 15px
            & + .price
              color: $accent-color
      .properties-block
        margin: 10px 0
        &__item
          margin-bottom: 10px
          @media (min-width: 768px)
            margin-bottom: 5px
          &:last-child
            margin-bottom: 0
        .property__title
          display: flex
          color: #999
          .property-value
            display: inline-block
            margin-left: 10px
            color: $txt-color
        .property__list
          display: flex
          flex-wrap: wrap
          margin-top: 5px
          .input-group
            flex: 0 0 auto
            margin: 5px
            &:nth-of-type(1)
              margin-left: 0
            label
              display: flex
              align-items: center
              justify-content: center
              position: relative
              padding: 4px
              min-width: 30px
              min-height: 30px
              input
                position: absolute
                top: 0
                left: 0
                visibility: hidden
                opacity: 0
              img
                display: block
              .border-line
                position: absolute
                top: 0
                left: 0
                width: 100%
                height: 100%
                border: 1px solid #eee
                transition: border-color .3s
              input:checked + .border-line
                border-color: $accent-color
              input:disabled + .border-line
                border-color: rgba(#e6e6e6, .3)
                background-color: rgba(#e6e6e6, .3)
                z-index: 1
      .addcart-block
        .addcart-form
          display: flex
          flex-wrap: wrap
          @media (min-width: 375px)
            flex-wrap: nowrap
            align-items: stretch
            margin-bottom: 15px
          @media (min-width: 768px)
            flex-wrap: wrap
            margin-bottom: 10px
          .input-group
            margin-right: 10px
            flex: 1 1 auto
            width: auto
            &:last-child
              margin-right: 0
            &.count-group
              display: flex
              align-items: stretch
              flex: 0 0 auto
              margin-right: 15px
              order: 1
              @media (min-width: 375px)
                margin-right: 10px
              .count-input
                display: block
                flex: 1 1 auto
                width: 46px
                max-width: 46px
                text-align: center
                border: 1px solid rgba($txt-color, .3)
              .btns
                display: flex
                flex-direction: column
                .count-btn
                  display: flex
                  align-items: center
                  justify-content: center
                  border: 1px solid rgba($txt-color, .3)
                  border-left: none
                  flex: 1 1 100%
                  padding: 0 6px
                  color: $txt-color
                  transition: color .3s
                  &:hover
                    color: $accent-color
                  &:nth-of-type(1)
                    border-bottom: none
            &.addbtns-group
              flex: 0 0 auto
              display: flex
              align-items: stretch
              order: 2
              @media (min-width: 414px)
                order: 4
                margin-left: 10px
              @media (min-width: 768px)
                order: 2
                margin-left: 0
              @media (min-width: 960px)
                order: 4
                margin-left: 10px
              @media (min-width: 1024px)
                order: 2
                margin-left: 0
              @media (min-width: 1200px)
                order: 4
                margin-left: 10px
              .addfavorite-link
                display: inline-flex
                align-items: center
                justify-content: center
                padding: 0 10px
                border: 1px solid #ccc
                font-size: 18px
                line-height: 18px
                color: rgba($txt-color, .5)
                transition: color .3s
                &:hover
                  color: $accent-color
                &:nth-of-type(1)
                  border-right: none
            &.btn-group
              flex: 1 1 100%
              margin-top: 15px
              order: 3
              @media (min-width: 375px)
                margin-top: 0
                button
                  margin-bottom: 0
                  padding: 10px 5px 11px
              @media (min-width: 576px)
                flex: 0 0 auto
                button
                  padding: 10px 15px 11px
              @media (min-width: 768px)
                flex: 1 1 100%
                margin-top: 10px
                button
                  max-width: 200px
              @media (min-width: 960px)
                flex: 0 0 auto
                margin-top: 0
              @media (min-width: 1024px)
                flex: 1 1 100%
                margin-top: 10px
              @media (min-width: 1200px)
                flex: 1 1 auto
                margin-top: 0
      .seller-info-block
        display: flex
        flex-direction: column
        border-top: 1px solid #eee
        padding-top: 15px
        @media (min-width: 768px)
          padding-top: 10px
        & > span
          margin-bottom: 5px
          color: #999
          & > span, & > a
            display: inline-block
            margin-left: 5px
            color: $txt-color
          & > a
            color: $accent-color
            text-decoration: none
        .seller-icon
          width: auto
          max-width: 250px
          display: block
          margin: 15px 0
          @media (max-width: 768px)
            max-width: 200px
          @media (min-width: 960px)
            max-width: 180px
    &__full
      width: 100%
      flex: 1 1 100%
      margin-top: 15px
      .descr-row
        margin-bottom: 20px
        &:last-child
          margin-bottom: 0
        &__title
          text-transform: uppercase
          color: #999
          font-size: 22px
          line-height: 22px
          font-weight: 500
          display: block
          width: 100%
          border-bottom: 1px solid #eee
          padding-bottom: 5px
          margin-bottom: 10px
        &__content
          font-size: 15px
          line-height: 20px
          p
            max-width: 600px
            margin-bottom: 10px
            &:last-child
              margin-bottom: 0
        &.properties
          .descr-row__content
            display: flex
            flex-direction: column
            align-items: flex-start
            justify-content: flex-start
            @media (min-width: 768px)
              flex-direction: row
              flex-wrap: wrap
          .properties__column
            flex: 1 1 100%
            width: 100%
            margin-bottom: 15px
            @media (min-width: 768px)
              flex: 0 0 calc(50% - 10px)
              margin-right: 20px
              &:nth-of-type(2n)
                margin-right: 0
            @media (min-width: 960px)
              flex: 0 0 calc(33.3% - 15px)
              margin-right: 20px
              margin-bottom: 25px
              &:nth-of-type(2n)
                margin-right: 20px
              &:nth-of-type(3n)
                margin-right: 0
            @media (min-width: 1200px)
              flex: 0 0 calc(33.3% - 30px)
              margin-right: 42px
              &:nth-of-type(2n)
                margin-right: 42px
              &:nth-of-type(3n)
                margin-right: 0
            .descr-list
              &__item
                display: flex
                flex-wrap: wrap
                justify-content: space-between
                border-bottom: 1px dotted #999
                margin-bottom: 15px
                &:last-child
                  margin-bottom: 0
                span
                  display: inline-block
                  background-color: #fff
                  margin-bottom: -7px
                  &:nth-of-type(1)
                    padding-right: 5px
                    color: #999
                  &:nth-of-type(2)
                    padding-left: 5px


/* enterprises page */
.enterprises-main
  background-repeat: no-repeat
  background-position: center
  background-size: cover
  background-color: #4e4846
  position: relative
  min-height: 100vh
  &:before
    content: ''
    display: block
    width: 100%
    height: 100%
    background-color: rgba(0,0,0,.3)
    position: absolute
    top: 0
    left: 0
  &__content
    display: flex
    flex-direction: column
    align-items: flex-start
    justify-content: flex-end
    flex-wrap: nowrap
    color: #fff
    position: relative
    z-index: 1
    min-height: calc(100vh - 80px)
    @include adaptive-value('padding-bottom', 60, 35, 1)
  &__title
    font-family: Montserrat, sans-serif
    font-weight: 600
    @include adaptive-value('font-size', 45, 18, 1)
    @include adaptive-value('line-height', 45, 24, 1)
    display: inline-block
    text-transform: uppercase
    span
      display: inline-block
  .search-form
    width: 100%
    margin: 10px 0 0
    @media (min-width: 360px)
      margin: 20px 0 0
    .input-group
      background-color: rgba(255,255,255,.9)
      display: flex
      button
        display: flex
        align-items: center
        justify-content: center
        border: none
        background-color: transparent
        font-size: 28px
        line-height: 20px
        padding: 0 10px
      input
        flex: 1 1 100%
        border: none
        border-radius: 0
        background-color: transparent
        padding: 10px
        font-size: 18px
        line-height: 20px
        font-weight: 100
        @include input-placeholder
          opacity: 1
          transition: opacity 0.3s ease
        &:focus
          @include input-placeholder
            opacity: 0
            transition: opacity 0.3s ease
.enterprises
  &__menu
    position: relative
    padding: 0 20px
    margin-top: 20px
    &__item
      display: block
      padding: 20px 0
      .menu-icon
        display: block
        width: auto
        max-width: 40px
        margin: 0 auto
        @media (min-width: 360px)
          max-width: 100%
      .menu-title
        display: block
        width: 100%
        text-align: center
        margin-top: 15px
        text-transform: uppercase
      &:hover, &.active
        .menu-title
          color: $accent-color
        .menu-icon
          filter: invert(31%) sepia(54%) saturate(6592%) hue-rotate(341deg) brightness(101%) contrast(99%)
    .owl-nav
      position: absolute
      top: calc(50% - 12px)
      left: 0
      width: 100%
      height: 1px
      display: flex
      justify-content: space-between
      .icon
        font-size: 24px
        line-height: 24px
        transition: color .3s
        &:hover
          color: $accent-color
  &__category
    display: flex
    align-items: stretch
    justify-content: flex-start
    flex-wrap: wrap
    margin: 10px 0
    &__item
      display: block
      flex: 1 1 100%
      width: auto
      border: 1px solid $txt-color
      background-color: transparent
      padding: 10px 20px
      text-align: center
      color: $txt-color
      position: relative
      overflow: hidden
      transition: border-color .3s, color .3s
      margin: 0 0 10px
      text-transform: uppercase
      @media (min-width: 640px)
        flex: 0 0 auto
        margin: 0 15px 15px 0
      @media (min-width: 768px)
        padding: 10px
        font-size: 15px
      span
        position: relative
        z-index: 2
      &:before
        content: ''
        display: block
        width: 100%
        height: 100%
        position: absolute
        background-color: $accent-color
        top: 0
        left: 0
        transform: translateY(100%)
        transition: transform .3s
      &:hover
        border-color: $accent-color
        color: #fff
        &:before
          transform: translateY(0)






















